@import url(partials/reset.css);
@import url(partials/fonts.css);
@import url(partials/simplebar.css);
/* my style */
.sh-burger {
  position: relative;
  display: none;
  width: 34px;
  height: 26px;
  cursor: pointer;
  transition-duration: .2s; }
  .sh-burger span,
  .sh-burger span::before,
  .sh-burger span::after {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 34px;
    height: 2px;
    margin-top: -1px;
    margin-left: -17px;
    background-color: #fff; }
  .sh-burger span::before,
  .sh-burger span::after {
    content: '';
    transition: 0.2s; }
  .sh-burger span::before {
    transform: translateY(-12px); }
  .sh-burger span::after {
    transform: translateY(12px); }
  .sh-burger--active {
    background: #F5F6FB; }
    .sh-burger--active span {
      height: 0; }
      .sh-burger--active span::before {
        transform: rotate(45deg);
        background: #323340; }
      .sh-burger--active span::after {
        transform: rotate(-45deg);
        background: #323340; }

input,
button,
textarea {
  font-size: inherit;
  font-family: inherit;
  margin: 0;
  padding: 0;
  border: 0; }

div,
input,
textarea,
select,
button,
h1,
h2,
h3,
h4,
h5,
h6,
a,
span {
  outline: none;
  font-size: inherit;
  background: transparent; }

* {
  box-sizing: border-box; }

a,
a:hover {
  text-decoration: none; }

img {
  display: block;
  max-width: 100%; }

textarea {
  resize: none; }

a {
  color: inherit; }

body {
  display: flex;
  flex-direction: column;
  margin: 0;
  color: #323340;
  background: #9ece9a;
  font-family: "Montserrat", sans-serif;
  font-size: 16px; }

.main {
  flex-grow: 1; }

html,
body {
  height: 100%; }

.container {
  box-sizing: border-box;
  width: 981px;
  max-width: 95%;
  max-width: calc(100% - 30px * 2);
  margin: 0 auto; }

@media screen and (max-width: 550px) {
  .container {
    max-width: calc(100% - 15px * 2); } }

#jGrowl {
  z-index: 999999; }

*:focus {
  outline: none; }

.hide {
  display: none; }

body {
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  -webkit-locale: auto;
  white-space: normal;
  color: #053B41;
  background: #e0edec;
  width: 450px; }
  body.compensate-for-scrollbar {
    overflow: hidden; }

html {
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%; }

.textarea-autosize {
  display: block;
  max-width: 100%;
  width: auto;
  resize: none;
  padding: 4px 6px 3px;
  background: #ffffff;
  border-radius: 4px;
  min-height: 45px;
  color: #053B41; }

.copyright {
  padding: 1px;
  font-size: 13px;
  line-height: 1;
  text-align: center;
  background: #9ECE9A; }

.simplebar-scrollbar:before {
  opacity: 1;
  background: #9ece9a;
  border-radius: 50px;
  border: 1px solid #000000;
  left: 6px;
  right: 0px; }

.simplebar-scrollbar.simplebar-visible:before {
  opacity: .7; }

.todo-list-wrap {
  height: 220vh;
  overflow: auto;
  max-height: 90vh;
  min-height: 300px;
  overflow-x: hidden; }

.todo-list {
  padding-right: 5px;
  display: flex;
  flex-direction: column-reverse; }

.todo-list-item {
  padding: 3px 3px;
  transition-duration: .2s;
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #074F57; }
  .todo-list-item__chb-wrap {
    margin-right: 3px;
    align-self: flex-start;
    margin-top: 0; }
  .todo-list-item__chb {
    display: none; }
    .todo-list-item__chb:checked + .todo-list-item__chb-pseudo::before {
      opacity: 1; }
  .todo-list-item__chb-pseudo {
    cursor: pointer;
    background: #000000;
    width: 45px;
    min-width: 45px;
    height: 45px;
    border-radius: 3px;
    display: block;
    position: relative;
    z-index: 1; }
    .todo-list-item__chb-pseudo::before {
      content: '';
      display: block;
      z-index: 2;
      position: absolute;
      width: calc(100% - 8px);
      height: calc(100% - 8px);
      top: 4px;
      left: 4px;
      background: url(../img/check_mark.svg) no-repeat center center;
      background-size: contain;
      opacity: 0;
      transition-duration: .2s; }
  .todo-list-item__content {
    flex-grow: 1; }
    .todo-list-item__content textarea {
      width: 100%;
      font-size: 19px;
      line-height: 1; }
  .todo-list-item__remove {
    margin-left: 6px;
    cursor: pointer;
    width: 20px;
    min-width: 20px;
    height: 20px;
    border-radius: 3px;
    display: block;
    position: relative;
    z-index: 1;
    background: url(../img/remove.svg) no-repeat center center;
    background-size: contain;
    align-self: flex-start;
    margin-top: 2px;
    transition-duration: .2s;
    opacity: .5; }
    .todo-list-item__remove:hover {
      opacity: 1; }
  .todo-list-item--completed {
    text-decoration: line-through;
    opacity: .7;
    order: -999999 !important; }

.todo-add-new {
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 1px solid #074F57;
  border-bottom: 1px solid #074F57;
  background: #9ece9a;
  padding: 4px 20px;
  transition-duration: .9s;
  cursor: pointer;
  order: 999999;
  /*position: sticky;
	top: 0;*/
  z-index: 9999; }
  .todo-add-new span {
    display: block;
    font-size: 21px;
    font-weight: 400;
    color: #000; }
  .todo-add-new::before {
    margin-right: 15px;
    content: '';
    width: 20px;
    min-width: 20px;
    height: 20px;
    display: block;
    background: url(../img/remove.svg) no-repeat center center;
    background-size: contain;
    transform: rotate(45deg);
    background: url(../img/remove.svg) no-repeat center center; }
  .todo-add-new:hover {
    opacity: .7; }
